import searchInput from "@/components/bm-searchInput/bm-searchInput.vue"
export default {
	components: {searchInput},
	data() {
		
		return {
			title: 'Hello',
			search_result_title:[],
			search_result_note:[],
			type:'',
			codeList:[],
			pageNo:1,
			pageSize:99,
			totalPage:0
			
		}
	},
	onLoad(option) {
		if(option.type){
			this.type = option.type;
			this.getCodeList();
		}else{
			
		}
		if(option.nav){
			// this.type = option.type;
			// this.getCodeList();
			
			uni.setNavigationBarTitle({
				title:option.nav
			})
		}else{
			
		}
	},
	methods: {
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		deleteMethod(s,type){
			let _this = this;
			uni.showModal({
				"title":"删除提示",
				"content":"是否确认要删除此二维码？",
				"success":function(resModal){
					if(resModal.confirm){
						// console.log(s);
						_this.$ajax("/"+type+"/remove","POST",{qrcodeNo:s.qrcodeNo}).then(res1=>{
							if(res1.code == "200"){
								_this.getCodeList()
								// _this.codeList.filter(res2=>{
								// 	console.log(res2.qrcodeNo);
								// 	console.log(s.qrcodeNo);
								// 	return res2.qrcodeNo != s.qrcodeNo
								// 	// if(){
								// 	// 	return false;
								// 	// }
								// })
								uni.showToast({
									"title":"删除成功",
									"icon":"none"
								})
							}
						})
						
						
					}else{
						uni.showToast({
							"title":"取消删除",
							"icon":"none"
						})
					}
				}
			})
		},
		editMethod(s,type){
			uni.showModal({
				"title":"编辑提示",
				"content":"是否进行二维码编辑？",
				"success":function(res){
					if(res.confirm){
						if(type == 'nav'){
							uni.navigateTo({
								"url":"/pages/address/msdh/zzdhm?data="+encodeURIComponent(JSON.stringify(s))
							})
							return false
						}
						
						if(type == 'move'){
							uni.navigateTo({
								"url":"/pages/address/msnc/zzncm?data="+encodeURIComponent(JSON.stringify(s))
							})
							return false
						}
						
						if(type == 'park'){
							uni.navigateTo({
								"url":"/pages/address/mszc/zztcm?data="+encodeURIComponent(JSON.stringify(s))
							})
							return false
						}
					}else{
						uni.showToast({
							"title":"取消编辑",
							"icon":"none"
						})
					}
				}
			})
		},
		get_selected_lists(){
			
		},
		finish_search(){
			
		},
		getCodeList(){
			let _this = this;
			this.$ajax("/"+this.type+"/page","POST",{pageNo:this.pageNo,pageSize:this.pageSize}).then(res=>{
				console.log(res);
				_this.codeList = []
				res.list.map((res1)=>{
					_this.codeList.push(res1);
				})
				// console.log()
				this.totalPage = res.totalPage
				// this.pageNo++;
			})
		},
		detail(type,id,url){
			uni.showToast({
				"title":'正在跳转'+type+'详情页',
				"icon":"none"
			})
			setTimeout(function(){
				uni.navigateTo({
					"url":url
				})
			},1500)
		}
		
	},
	filters:{
		changeNull(value){
			console.log(value);
			if(value == null){
				return "--";
			}else{
				return value;
			}
		}
	}
}
